/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.RangeSliderContainer {
  width: 300px;
  margin-left: 10px;
  margin-right: 10px; }
  .RangeSliderContainer .rangeslider {
    margin: 0; }
    .RangeSliderContainer .rangeslider .rangeslider__handle {
      height: 10px;
      width: 10px; }
      .RangeSliderContainer .rangeslider .rangeslider__handle:after {
        display: none; }
    .RangeSliderContainer .rangeslider .rangeslider__fill {
      background-image: -webkit-linear-gradient(top, #89cdef 0, #81bfde 100%);
      background-image: -o-linear-gradient(top, #89cdef 0, #81bfde 100%);
      background-image: linear-gradient(to bottom, #89cdef 0, #81bfde 100%);
      background-repeat: repeat-x; }
