/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

th.sort-des:after {
  content: ' \25BC'; }

th.sort-asc:after {
  content: ' \25B2'; }

.cbioportal-frontend .table > thead > tr > th.multilineHeader {
  vertical-align: top;
  line-height: 1.1;
  padding-bottom: 4px; }

.topPagination {
  display: flex;
  align-items: center;
  justify-content: center; }
  .topPagination .textBetweenButtons {
    opacity: 1 !important; }

.tableMainToolbar .form-group {
  margin-bottom: 5px; }

.cbioportal-frontend .table > tbody > tr > td.columnResizer,
.cbioportal-frontend .table > thead > tr > td.columnResizer {
  width: 2px;
  border-right: #ccc 1px solid;
  border-left: #ccc 1px solid;
  padding: 0 !important; }
