/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.oncoprint__controls {
  display: flex; }
  .oncoprint__controls .rangeslider {
    margin: 0; }
    .oncoprint__controls .rangeslider .rangeslider__labels {
      display: none; }
    .oncoprint__controls .rangeslider .rangeslider__handle {
      height: 10px;
      width: 10px; }
      .oncoprint__controls .rangeslider .rangeslider__handle:after {
        display: none; }
  .oncoprint__controls .checkbox:first-child,
  .oncoprint__controls .radio:first-child {
    margin-top: 0px; }
  .oncoprint__controls .dropdown-menu {
    width: 270px;
    padding: 12px !important; }
    .oncoprint__controls .dropdown-menu > *,
    .oncoprint__controls .dropdown-menu button {
      margin-bottom: 10px;
      width: 100%; }
    .oncoprint__controls .dropdown-menu > :last-child {
      margin-bottom: 0 !important; }
    .oncoprint__controls .dropdown-menu.heatmap {
      width: 450px; }
  .oncoprint__controls .oncoprint__controls__heatmap_menu textarea {
    width: 100%;
    min-height: 50px;
    border: 1px solid #ddd;
    resize: vertical; }
    .oncoprint__controls .oncoprint__controls__heatmap_menu textarea.treatment-textarea {
      border-radius: 2px; }
  .oncoprint__controls .oncoprint__controls__heatmap_menu > *,
  .oncoprint__controls .oncoprint__controls__heatmap_menu button {
    margin-bottom: 10px;
    width: 100%; }
  .oncoprint__controls .oncoprint__controls__heatmap_menu > :last-child {
    margin-bottom: 0 !important; }
  .oncoprint__controls .oncoprint__controls__heatmap_menu .treatment-selector button {
    width: auto;
    margin: 0;
    padding: 5px 10px;
    vertical-align: middle; }
  .oncoprint__controls .oncoprint__controls__heatmap_menu .text-icon-area {
    width: 100%;
    min-height: 50px;
    overflow: hidden;
    margin-bottom: 5px; }
    .oncoprint__controls .oncoprint__controls__heatmap_menu .text-icon-area .icon-area {
      min-height: 0px;
      overflow: hidden;
      padding: 1px;
      padding-bottom: 2px; }
      .oncoprint__controls .oncoprint__controls__heatmap_menu .text-icon-area .icon-area .icon {
        padding: 1px;
        padding-left: 7px;
        padding-right: 7px;
        margin: 2px;
        margin-bottom: 3px;
        background: #3786c2;
        color: white;
        border-radius: 4px;
        float: left; }
    .oncoprint__controls .oncoprint__controls__heatmap_menu .text-icon-area .text-area {
      width: 100%;
      min-height: 50px;
      resize: vertical;
      overflow: hidden;
      border: 1px solid #ddd; }
  .oncoprint__controls .oncoprint__zoom-controls {
    display: flex !important;
    align-items: center; }
    .oncoprint__controls .oncoprint__zoom-controls > * {
      margin-right: 10px; }
    .oncoprint__controls .oncoprint__zoom-controls > :last-child {
      margin-right: 0; }
  .oncoprint__controls .Select {
    margin-right: 10px; }
    .oncoprint__controls .Select .Select-control {
      height: 32px; }
    .oncoprint__controls .Select .Select-input {
      height: 32px; }
  .oncoprint__controls .clinical-track-selector .Select-control {
    height: 33px; }
  .oncoprint__controls .clinical-track-selector .Select .Select-placeholder {
    color: #000; }
  .oncoprint__controls .clinical-track-selector .Select .Select-arrow {
    border-color: #000 transparent transparent; }
  .oncoprint__controls .clinical-track-selector .Select .Select-menu-outer {
    max-height: 400px; }
  .oncoprint__controls .clinical-track-selector .Select .Select-menu {
    max-height: 398px;
    overflow: hidden; }
    .oncoprint__controls .clinical-track-selector .Select .Select-menu > div > div:nth-child(2) {
      max-height: 350px !important;
      overflow-x: hidden !important;
      overflow-y: scroll !important; }
  .oncoprint__controls .clinical-track-selector .is-open .Select-control .Select-arrow {
    border-color: transparent transparent #000; }
